.App {
  min-height: 100vh;
  text-align: center;
  background-color: #FAFAFA;
  color: #212121;
  display: flex;
  flex-direction: row;

  & > textarea, & > section {
    width: 50%;
    min-height: 100vh;
  }
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
